/* Fonts */
@font-face {
  font-family: "Kaushan Script";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/KaushanScript-Regular.woff2") format("woff2"), url("../fonts/KaushanScript-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: italic;
  font-weight: 400;
  src: url("../fonts/Roboto-Italic.woff2") format("woff2"), url("../fonts/Roboto-Italic.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: italic;
  font-weight: 300;
  src: url("../fonts/Roboto-LightItalic.woff2") format("woff2"), url("../fonts/Roboto-LightItalic.woff") format("woff");
  font-display: swap;
}
/* Normalize */
body {
  margin: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

p {
  margin: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

/* Container */
.container {
  max-width: 1230px;
  margin: 0 auto;
  padding: 0 15px;
}

/* Page */
.page {
  font-family: "Montserrat";
  font-weight: normal;
  font-size: 16px;
  line-height: 1.6;
  background: linear-gradient(45deg, #49a09d, #5f2c82);
}

.page__body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

a {
  text-decoration: none;
}

/* Page Header */
.page-header {
  background-color: #fff;
  border-radius: 15px;
  margin: 0 15px;
  text-align: center;
}

/* Page Main */
.page-main {
  flex-grow: 1;
}

.page-main__task {
  margin-top: 10px;
}

/* Page Footer */
/* Task */
.task {
  background-color: #fff;
  border-radius: 15px;
  padding: 15px 15px;
  margin: 15px;
}

/* Table */
.table {
  width: 100%;
  border-collapse: collapse;
  overflow: hidden;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
}

.table thead th {
  background-color: #55608f;
  color: #fff;
}

.table tr,
.table th,
.table td {
  border: 1px solid #55608f;
}

.table__number {
  text-align: center;
}

.table td:not(.table__number) {
  padding-left: 5px;
}

/* Game */
.game {
  display: flex;
  justify-content: center;
  padding: 15px;
}

.game__table {
  border-collapse: collapse;
  font-family: "Roboto", sans-serif;
  font-size: 24px;
  font-style: italic;
  position: relative;
}

.game__cell {
  width: 5em;
  height: 5em;
  border: 1px solid #000;
  text-align: center;
}